export default{
    data(){
        return {
            info: {}
        }
    },
    onLoad(){
        this.getList();
    },
    onShow(){
        this.getList();
    },
    methods:{
        getList(){
            this.$http.get('/User/userInfo')
            .then(res=>{
                this.info = res.data;
            })
            .catch(err=>{
                uni.showToast({
                    icon: 'none',
                    title: '请重新登录'
                })
            })
        },
        changeAvatar(){
            let that = this;
            uni.chooseImage({ 
                count: 1,
                success: res=>{
                    this.$http.uploadFile(res.tempFilePaths[0]).then(res=>{
                        this.$http.put('/Person/HeadImg',res.data)
                        .then(res=>{
                            uni.showToast({
                                icon: 'success',
                                title: '修改成功!',
                                success: res=>{
                                    that.getList();
                                }
                            })
                        })
                        .catch(err=>{
                            uni.showToast({
                                icon: 'none',
                                title: '系统错误，请稍后再试!'
                            })
                        })
                    })
                    .catch(err=>{
                            uni.showToast({
                                icon: 'none',
                                title: '系统错误，请稍后再试!'
                            })
                    })
                }
            })
        },
        changeInfo(){
            uni.navigateTo({
                url: 'changeInfo'
            })
        },
        changeIdCard(){
            uni.navigateTo({
                url: 'changeIdCard'
            })
        },
        changePhoneNumber(){
            uni.navigateTo({
                url: 'changePhoneNumber'
            })
        },
        changePassword(){
            uni.navigateTo({
                url: 'changePassword'
            })
        },
        enterDetail(){
            uni.navigateTo({
                url: 'myCommission'
            })
        },
        // 退出
        exit(){
            uni.showModal({
                title: '提示',
                content: '确认退出登录？',
                success: res=>{
                    if(res.confirm){
                        uni.removeStorageSync('token');
                        uni.redirectTo({
                            url: '../login/login'
                        })
                    }
                }
            })
        }
    }
}
